"use client"

import React, { useMemo, useState } from "react"
import Link from "next/link"
import {
  BadgeCheck,
  CircleAlert,
  FileSearch,
  Filter,
  MessageSquareText,
  Plus,
  Search,
  Shield,
  ThumbsDown,
  ThumbsUp,
  Wand2,
} from "lucide-react"

import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import { Card, CardContent } from "@/components/ui/card"
import { Separator } from "@/components/ui/separator"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Dialog, DialogContent, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { Textarea } from "@/components/ui/textarea"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"

type Framework = "SOX" | "SOC" | "ISO" | "PCI"
type KpiStatus = "GREEN" | "YELLOW" | "RED"
type ReviewStatus = "PENDING" | "APPROVED" | "REJECTED" | "CHANGES_REQUESTED"

type ReviewItem = {
  id: string
  framework: Framework
  controlCode: string
  controlName: string
  kpiName: string
  periodLabel: string
  ownerArea: string
  focalName: string
  submittedAt: string // dd/mm/yyyy
  status: KpiStatus
  severity: "LOW" | "MED" | "HIGH" | "CRITICAL"
  evidenceType: "Link" | "Arquivo" | "Texto"
  evidenceValue: string
  justification?: string
  reviewerNote?: string
  reviewStatus: ReviewStatus
}

function kpiStatusBadge(s: KpiStatus) {
  if (s === "GREEN") return <Badge className="bg-emerald-600 text-white">Green</Badge>
  if (s === "YELLOW") return <Badge className="bg-amber-500 text-white">Yellow</Badge>
  return <Badge className="bg-rose-600 text-white">Red</Badge>
}

function severityBadge(s: ReviewItem["severity"]) {
  const map: Record<ReviewItem["severity"], { label: string; className: string }> = {
    LOW: { label: "LOW", className: "bg-muted text-foreground" },
    MED: { label: "MED", className: "bg-amber-100 text-amber-900" },
    HIGH: { label: "HIGH", className: "bg-orange-100 text-orange-900" },
    CRITICAL: { label: "CRITICAL", className: "bg-rose-100 text-rose-900" },
  }
  const cfg = map[s]
  return <Badge className={cfg.className}>{cfg.label}</Badge>
}

function reviewStatusBadge(s: ReviewStatus) {
  const map: Record<ReviewStatus, { label: string; className: string }> = {
    PENDING: { label: "Pendente", className: "bg-primary/10 text-primary" },
    APPROVED: { label: "Aprovado", className: "bg-emerald-100 text-emerald-900" },
    REJECTED: { label: "Reprovado", className: "bg-rose-100 text-rose-900" },
    CHANGES_REQUESTED: { label: "Ajustes", className: "bg-amber-100 text-amber-900" },
  }
  const cfg = map[s]
  return <Badge className={cfg.className}>{cfg.label}</Badge>
}

const MOCK: ReviewItem[] = [
  {
    id: "rvw-001",
    framework: "SOX",
    controlCode: "TEC_C50",
    controlName: "Logical Access Granting",
    kpiName: "100% acessos aprovados via fluxo",
    periodLabel: "Fev/2026",
    ownerArea: "TI Infra",
    focalName: "João Mendes",
    submittedAt: "09/02/2026",
    status: "YELLOW",
    severity: "HIGH",
    evidenceType: "Link",
    evidenceValue: "https://exemplo.com/evidencia/tec_c50_fev_2026",
    justification: "Dois acessos foram aprovados com atraso por indisponibilidade do aprovador.",
    reviewStatus: "PENDING",
  },
  {
    id: "rvw-002",
    framework: "SOX",
    controlCode: "TEC_C91",
    controlName: "Periodic Access Review (Security Tools)",
    kpiName: "Revisão concluída até D+10",
    periodLabel: "Jan/2026",
    ownerArea: "Security",
    focalName: "Fillipe Nunes",
    submittedAt: "07/02/2026",
    status: "GREEN",
    severity: "MED",
    evidenceType: "Texto",
    evidenceValue: "Checklist preenchido + evidência anexada no repositório interno.",
    reviewStatus: "PENDING",
  },
  {
    id: "rvw-003",
    framework: "ISO",
    controlCode: "ISO_A.8.12",
    controlName: "Data Leakage Prevention Monitoring",
    kpiName: "Cobertura DLP >= 95%",
    periodLabel: "Fev/2026",
    ownerArea: "Security",
    focalName: "Ricardo Lima",
    submittedAt: "08/02/2026",
    status: "RED",
    severity: "CRITICAL",
    evidenceType: "Arquivo",
    evidenceValue: "dlp_coverage_fev_2026.csv",
    justification: "Queda de cobertura por falha no deploy do agente em lote.",
    reviewStatus: "PENDING",
  },
]

export default function ReviewQueuePage() {
  const [framework, setFramework] = useState<Framework | "ALL">("ALL")
  const [query, setQuery] = useState("")
  const [statusFilter, setStatusFilter] = useState<KpiStatus | "ALL">("ALL")
  const [severityFilter, setSeverityFilter] = useState<ReviewItem["severity"] | "ALL">("ALL")
  const [selected, setSelected] = useState<ReviewItem | null>(null)

  const [decision, setDecision] = useState<ReviewStatus>("APPROVED")
  const [reviewerNote, setReviewerNote] = useState("")

  const filtered = useMemo(() => {
    const q = query.trim().toLowerCase()
    return MOCK.filter((r) => {
      if (r.reviewStatus !== "PENDING") return false
      if (framework !== "ALL" && r.framework !== framework) return false
      if (statusFilter !== "ALL" && r.status !== statusFilter) return false
      if (severityFilter !== "ALL" && r.severity !== severityFilter) return false
      if (!q) return true
      const hay = `${r.controlCode} ${r.controlName} ${r.kpiName} ${r.ownerArea} ${r.focalName} ${r.periodLabel}`.toLowerCase()
      return hay.includes(q)
    })
  }, [framework, query, statusFilter, severityFilter])

  const counts = useMemo(() => {
    const allPending = MOCK.filter((r) => r.reviewStatus === "PENDING")
    const red = allPending.filter((r) => r.status === "RED").length
    const yellow = allPending.filter((r) => r.status === "YELLOW").length
    const green = allPending.filter((r) => r.status === "GREEN").length
    const critical = allPending.filter((r) => r.severity === "CRITICAL").length
    return { total: allPending.length, red, yellow, green, critical }
  }, [])

  function openDetail(item: ReviewItem) {
    setSelected(item)
    setDecision("APPROVED")
    setReviewerNote(item.reviewerNote ?? "")
  }

  function handleApplyDecision() {
    // placeholder: aqui você vai chamar API e atualizar status no banco
    setSelected(null)
    setReviewerNote("")
  }

  function clearFilters() {
    setFramework("ALL")
    setQuery("")
    setStatusFilter("ALL")
    setSeverityFilter("ALL")
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex flex-col gap-3 sm:flex-row sm:items-start sm:justify-between">
        <div className="space-y-1">
          <h1 className="text-3xl font-bold tracking-tight text-foreground">Fila de Revisão</h1>
          <p className="text-sm text-muted-foreground">
            Valide execuções registradas pelos pontos focais e garanta rastreabilidade auditável.
          </p>
        </div>

        <div className="flex items-center gap-2">
          <Button variant="outline" className="gap-2 bg-transparent">
            <Filter className="h-4 w-4" />
            Exportar
          </Button>
          <Button className="gap-2">
            <Wand2 className="h-4 w-4" />
            Regras de revisão
          </Button>
        </div>
      </div>

      {/* Quick stats */}
      <div className="grid gap-4 md:grid-cols-4">
        <Card>
          <CardContent className="p-4">
            <div className="text-xs text-muted-foreground">PENDENTES</div>
            <div className="mt-2 flex items-end justify-between">
              <div className="text-3xl font-bold">{counts.total}</div>
              <Badge className="bg-primary/10 text-primary">Fila</Badge>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-4">
            <div className="text-xs text-muted-foreground">RED</div>
            <div className="mt-2 flex items-end justify-between">
              <div className="text-3xl font-bold text-rose-600">{counts.red}</div>
              <CircleAlert className="h-5 w-5 text-rose-600" />
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-4">
            <div className="text-xs text-muted-foreground">YELLOW</div>
            <div className="mt-2 flex items-end justify-between">
              <div className="text-3xl font-bold text-amber-600">{counts.yellow}</div>
              <CircleAlert className="h-5 w-5 text-amber-600" />
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-4">
            <div className="text-xs text-muted-foreground">CRÍTICOS</div>
            <div className="mt-2 flex items-end justify-between">
              <div className="text-3xl font-bold">{counts.critical}</div>
              <Badge className="bg-rose-100 text-rose-900">Critical</Badge>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Filters */}
      <Card>
        <CardContent className="p-4">
          <div className="flex flex-col gap-3 lg:flex-row lg:items-center">
            <div className="relative flex-1">
              <Search className="pointer-events-none absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
              <Input
                value={query}
                onChange={(e) => setQuery(e.target.value)}
                placeholder="Buscar por controle, KPI, owner, ponto focal ou período..."
                className="h-10 pl-10"
              />
            </div>

            <div className="grid grid-cols-2 gap-3 lg:flex lg:items-center">
              <Select value={framework} onValueChange={(v) => setFramework(v as any)}>
                <SelectTrigger className="h-10 w-full lg:w-40">
                  <SelectValue placeholder="Framework" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="ALL">Framework (Todos)</SelectItem>
                  <SelectItem value="SOX">SOX</SelectItem>
                  <SelectItem value="SOC">SOC</SelectItem>
                  <SelectItem value="ISO">ISO</SelectItem>
                  <SelectItem value="PCI">PCI</SelectItem>
                </SelectContent>
              </Select>

              <Select value={statusFilter} onValueChange={(v) => setStatusFilter(v as any)}>
                <SelectTrigger className="h-10 w-full lg:w-44">
                  <SelectValue placeholder="Status" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="ALL">Status (Todos)</SelectItem>
                  <SelectItem value="GREEN">Green</SelectItem>
                  <SelectItem value="YELLOW">Yellow</SelectItem>
                  <SelectItem value="RED">Red</SelectItem>
                </SelectContent>
              </Select>

              <Select value={severityFilter} onValueChange={(v) => setSeverityFilter(v as any)}>
                <SelectTrigger className="h-10 w-full lg:w-52">
                  <SelectValue placeholder="Criticidade" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="ALL">Criticidade (Todas)</SelectItem>
                  <SelectItem value="LOW">LOW</SelectItem>
                  <SelectItem value="MED">MED</SelectItem>
                  <SelectItem value="HIGH">HIGH</SelectItem>
                  <SelectItem value="CRITICAL">CRITICAL</SelectItem>
                </SelectContent>
              </Select>

              <Button variant="ghost" className="h-10 justify-start text-primary" onClick={clearFilters}>
                Limpar filtros
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Table */}
      <Card>
        <CardContent className="p-0">
          <div className="flex items-center justify-between px-4 py-3">
            <div className="flex items-center gap-2 text-sm text-muted-foreground">
              <FileSearch className="h-4 w-4" />
              <span>{filtered.length} item(ns) pendente(s) de revisão</span>
            </div>

            <Button variant="outline" className="gap-2 bg-transparent" asChild>
              <Link href="/execucao">
                <Plus className="h-4 w-4" />
                Registrar execução
              </Link>
            </Button>
          </div>

          <Separator />

          <div className="overflow-x-auto">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Controle</TableHead>
                  <TableHead>Framework</TableHead>
                  <TableHead>KPI</TableHead>
                  <TableHead>Período</TableHead>
                  <TableHead>Owner / Ponto focal</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead>Criticidade</TableHead>
                  <TableHead>Enviado em</TableHead>
                  <TableHead className="text-right">Ações</TableHead>
                </TableRow>
              </TableHeader>

              <TableBody>
                {filtered.map((r) => (
                  <TableRow key={r.id} className="align-top">
                    <TableCell>
                      <div className="text-xs text-muted-foreground">{r.controlCode}</div>
                      <div className="font-semibold">{r.controlName}</div>
                    </TableCell>

                    <TableCell>
                      <Badge className="bg-muted text-foreground">{r.framework}</Badge>
                    </TableCell>

                    <TableCell>
                      <div className="font-medium">{r.kpiName}</div>
                      <div className="mt-1 text-xs text-muted-foreground">
                        Evidência: <span className="font-medium">{r.evidenceType}</span>
                      </div>
                    </TableCell>

                    <TableCell className="font-medium">{r.periodLabel}</TableCell>

                    <TableCell>
                      <div className="font-semibold">{r.ownerArea}</div>
                      <div className="text-xs text-muted-foreground">{r.focalName}</div>
                    </TableCell>

                    <TableCell>{kpiStatusBadge(r.status)}</TableCell>

                    <TableCell>{severityBadge(r.severity)}</TableCell>

                    <TableCell className="text-muted-foreground">{r.submittedAt}</TableCell>

                    <TableCell className="text-right">
                      <Dialog>
                        <DialogTrigger asChild>
                          <Button variant="outline" className="bg-transparent" onClick={() => openDetail(r)}>
                            Detalhar
                          </Button>
                        </DialogTrigger>

                        <DialogContent className="max-w-3xl">
                          <DialogHeader>
                            <DialogTitle className="flex items-center justify-between gap-3">
                              <span>Revisão da Execução</span>
                              <span className="flex items-center gap-2">
                                {reviewStatusBadge(r.reviewStatus)}
                                {kpiStatusBadge(r.status)}
                              </span>
                            </DialogTitle>
                          </DialogHeader>

                          <div className="grid gap-4 md:grid-cols-2">
                            <Card>
                              <CardContent className="p-4 space-y-2">
                                <div className="text-xs text-muted-foreground">Controle</div>
                                <div className="font-semibold">{r.controlCode} — {r.controlName}</div>
                                <div className="text-xs text-muted-foreground">Framework</div>
                                <div className="flex items-center gap-2">
                                  <Badge className="bg-muted text-foreground">{r.framework}</Badge>
                                  {severityBadge(r.severity)}
                                </div>
                                <Separator className="my-2" />
                                <div className="text-xs text-muted-foreground">KPI</div>
                                <div className="font-medium">{r.kpiName}</div>
                                <div className="text-xs text-muted-foreground">Período</div>
                                <div className="font-medium">{r.periodLabel}</div>
                              </CardContent>
                            </Card>

                            <Card>
                              <CardContent className="p-4 space-y-2">
                                <div className="text-xs text-muted-foreground">Owner / Ponto focal</div>
                                <div className="font-semibold">{r.ownerArea}</div>
                                <div className="text-sm text-muted-foreground">{r.focalName}</div>
                                <Separator className="my-2" />
                                <div className="text-xs text-muted-foreground">Evidência</div>
                                <div className="rounded-md border border-border bg-muted/40 p-3 text-sm">
                                  <div className="flex items-center justify-between gap-2">
                                    <span className="font-medium">{r.evidenceType}</span>
                                    {r.evidenceType === "Link" ? (
                                      <Button variant="outline" className="bg-transparent h-8" asChild>
                                        <a href={r.evidenceValue} target="_blank" rel="noreferrer">
                                          Abrir
                                        </a>
                                      </Button>
                                    ) : (
                                      <Button variant="outline" className="bg-transparent h-8">
                                        Baixar
                                      </Button>
                                    )}
                                  </div>
                                  <div className="mt-2 break-words text-muted-foreground">{r.evidenceValue}</div>
                                </div>
                              </CardContent>
                            </Card>
                          </div>

                          {r.justification && (
                            <Card>
                              <CardContent className="p-4">
                                <div className="flex items-center gap-2">
                                  <MessageSquareText className="h-4 w-4 text-muted-foreground" />
                                  <div className="text-sm font-semibold">Justificativa do ponto focal</div>
                                </div>
                                <p className="mt-2 text-sm text-muted-foreground leading-relaxed">{r.justification}</p>
                              </CardContent>
                            </Card>
                          )}

                          <Card>
                            <CardContent className="p-4 space-y-3">
                              <div className="text-sm font-semibold flex items-center gap-2">
                                <Shield className="h-4 w-4 text-muted-foreground" />
                                Decisão do revisor (Security)
                              </div>

                              <div className="grid gap-3 md:grid-cols-2">
                                <Select value={decision} onValueChange={(v) => setDecision(v as ReviewStatus)}>
                                  <SelectTrigger className="h-10">
                                    <SelectValue placeholder="Selecione a decisão" />
                                  </SelectTrigger>
                                  <SelectContent>
                                    <SelectItem value="APPROVED">Aprovar</SelectItem>
                                    <SelectItem value="CHANGES_REQUESTED">Pedir ajustes</SelectItem>
                                    <SelectItem value="REJECTED">Reprovar</SelectItem>
                                  </SelectContent>
                                </Select>

                                <Button variant="outline" className="gap-2 bg-transparent justify-start">
                                  <BadgeCheck className="h-4 w-4" />
                                  Abrir Plano de Ação (placeholder)
                                </Button>
                              </div>

                              <Textarea
                                value={reviewerNote}
                                onChange={(e) => setReviewerNote(e.target.value)}
                                placeholder="Comentário do revisor (ex.: evidência incompleta, necessidade de detalhamento, referência de auditoria etc.)"
                                className="min-h-[90px]"
                              />

                              <div className="flex flex-wrap gap-2">
                                <Badge className="bg-muted text-foreground">
                                  SLA sugestão: D+5 para revisão
                                </Badge>
                                <Badge className="bg-muted text-foreground">
                                  Segregação: executor ≠ revisor
                                </Badge>
                              </div>
                            </CardContent>
                          </Card>

                          <DialogFooter className="gap-2 sm:gap-0">
                            <Button variant="outline" className="bg-transparent" onClick={() => setSelected(null)}>
                              Cancelar
                            </Button>

                            {decision === "REJECTED" ? (
                              <Button className="gap-2" onClick={handleApplyDecision}>
                                <ThumbsDown className="h-4 w-4" />
                                Reprovar
                              </Button>
                            ) : decision === "CHANGES_REQUESTED" ? (
                              <Button className="gap-2" onClick={handleApplyDecision}>
                                <CircleAlert className="h-4 w-4" />
                                Pedir ajustes
                              </Button>
                            ) : (
                              <Button className="gap-2" onClick={handleApplyDecision}>
                                <ThumbsUp className="h-4 w-4" />
                                Aprovar
                              </Button>
                            )}
                          </DialogFooter>
                        </DialogContent>
                      </Dialog>
                    </TableCell>
                  </TableRow>
                ))}

                {filtered.length === 0 && (
                  <TableRow>
                    <TableCell colSpan={9} className="py-10 text-center text-sm text-muted-foreground">
                      Nenhum item pendente com os filtros atuais.
                    </TableCell>
                  </TableRow>
                )}
              </TableBody>
            </Table>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
